import React, { useState } from "react";
import { Layout, Menu } from "antd";
import {
  UserOutlined,
  LaptopOutlined,
  NotificationOutlined,
} from "@ant-design/icons";

import Option1 from "../components/Options";
import MenuContext from "antd/lib/menu/MenuContext";
import MenuDivider from "antd/lib/menu/MenuDivider";
import MenuItem from "antd/lib/menu/MenuItem";

const { SubMenu } = Menu;
const { Header, Sider } = Layout;

function Main() {
  const [leftMenu, setLeftMenu] = useState("1");
  const [topMenu, setTopMenu] = useState("1");

  const handleTopMenuClick = (value) => {
    console.log("handleTopMenuClick : ", value);
    setTopMenu(value.key);
  };
  function handleLeftMenuClick(value) {
    console.log("handleLeftMenuClick : ", value);
    setLeftMenu(value.key);
  }

  return (
    <Layout>
      <Header className="header">
        <div className="logo" />
        <Menu
          theme="dark"
          mode="horizontal"
          defaultSelectedKeys={["1"]}
          onClick={handleTopMenuClick}
        >
          <Menu.Item key="1"></Menu.Item>
          <Menu.Item key="2"></Menu.Item>
          {/* <Menu.Item key="3">nav 3</Menu.Item> */}
        </Menu>
      </Header>

      <Layout>
        <Sider width={200} className="site-layout-background">
          <Menu
            mode="inline"
            defaultSelectedKeys={["1"]}
            defaultOpenKeys={["sub1"]}
            style={{ height: "100%", borderRight: 0 }}
            onClick={handleLeftMenuClick}
          >
            <SubMenu key="sub1" icon={<UserOutlined />} title="">
              <Menu.Item key="1"></Menu.Item>
              <Menu.Item key="2"></Menu.Item>
              {/* <Menu.Item key="3"></Menu.Item> */}
              {/* <Menu.Item key="4"></Menu.Item> */}
              {/* <Menu.Item key="5"></Menu.Item> */}
            </SubMenu>

            <SubMenu
              key="sub4"
              icon={<NotificationOutlined />}
              title=""
            >
              {/* <Menu.Item key="12"></Menu.Item>
              <Menu.Item key="13"></Menu.Item>
              <Menu.Item key="15"></Menu.Item> */}
              <Menu.Item key="16"></Menu.Item>
              <Menu.Item key="17"></Menu.Item>
              {/* <Menu.Item key="18"></Menu.Item>
              <Menu.Item key="19"></Menu.Item> */}
            </SubMenu>

            <SubMenu
              key="sub6"
              icon={<NotificationOutlined />}
              title=""
            >
              {/* <Menu.Item key="21"></Menu.Item> */}
              <Menu.Item key="22"></Menu.Item>
            </SubMenu>

            <SubMenu
              key="sub0"
              icon={<NotificationOutlined />}
              title=""
            >
              {/* <Menu.Item key="21"></Menu.Item> */}
              {/* <Menu.Item key="-1"></Menu.Item> */}
              <Menu.Item key="-2"></Menu.Item>
              {/* <Menu.Item key="-3"></Menu.Item> */}
            </SubMenu>
          </Menu>
        </Sider>

        <Option1 menuKey={leftMenu} />
      </Layout>

      {/* <Layout>
        <Sider>
          <Menu
            mode="inline"
            defaultSelectedKeys={["1"]}
            defaultOpenKeys={["sub1"]}
            style={{ height: "100%", borderRight: 0 }}
            onClick={handleLeftMenuClick}
          >
            <SubMenu icon={<UserOutlined />} title="">
              <MenuItem>eee</MenuItem>
            </SubMenu>
          </Menu>
        </Sider>
      </Layout> */}
    </Layout>
  );
}

export default Main;
